import {
  PropItem,
  PropItemTypes,
  UI_COLUMN,
  UI_DISABLED,
  UI_HIDDEN,
  UI_OPTIONS,
  UI_WIDGET,
  UiSchemaItem,
  UiWidgets
} from '../../types/common-types'
import { Slots } from 'vue'

export const renderFormItem = (
  form: { [key: string]: any },
  prop: string,
  item: PropItem,
  uiItem: UiSchemaItem = {},
  defaultSpan: number,
  onChange: (key: string, value: any) => void,
  slots: Slots
): JSX.Element | null => {
  if (uiItem[UI_HIDDEN]) {
    return null
  }
  const { type, oneOf, anyOf, format } = item
  const commonProps = uiItem[UI_OPTIONS] || {}
  commonProps.disabled = (uiItem[UI_DISABLED] === true)
  // placeholder
  if (!commonProps.placeholder) {
    commonProps.placeholder = item.title
  }

  const generateItem = () => {
    if (slots[prop]) {
      return () => slots[prop]?.(form)
    }
    switch (type) {
      case PropItemTypes.STRING: {
        if (!oneOf && !format) {
          const uiWidget = uiItem[UI_WIDGET] || 'input'
          if (uiWidget === UiWidgets.INPUT) {
            return (
              <el-input
                v-model={form[prop]}
                onInput={(value: any) => onChange(prop, value)}
                {...commonProps}
              />
            )
          } else if (uiWidget === UiWidgets.TEXTAREA) {
            return (
              <el-input
                v-model={form[prop]}
                rows={2}
                type="textarea"
                onInput={(value: any) => onChange(prop, value)}
                {...commonProps}
              />
            )
          } else {
            throw Error(' input  textarea')
          }
        }
        if (format === 'date') {
          return (
            <el-date-picker
              v-model={form[prop]}
              type="date"
              onChange={(value: any) => onChange(prop, value)}
              {...commonProps}
            />
          )
        }
        // if (format === 'time') {
        //   return (
        //     <el-time-picker
        //       v-model={form[prop]}
        //       {...commonProps}
        //     />
        //   )
        // }
        if (oneOf && oneOf.length > 0) {
          const uiWidget = uiItem[UI_WIDGET] || 'select'
          if (uiWidget === UiWidgets.SELECT) {
            return (
              <el-select
                v-model={form[prop]}
                onChange={(value: any) => onChange(prop, value)}
                {...commonProps}>
                {
                  oneOf.map(one => (
                    <el-option label={one.title} value={one.const}/>
                  ))
                }
              </el-select>
            )
          } else if (uiWidget === UiWidgets.RADIO) {
            return (
              <el-radio-group
                v-model={form[prop]}
                onChange={(value: any) => onChange(prop, value)}
                {...commonProps}>
                {
                  oneOf.map(one => (
                    <el-radio label={one.const}>{one.title}</el-radio>
                  ))
                }
              </el-radio-group>
            )
          } else {
            throw Error('oneOf  radio  select')
          }
        }
        break
      }
      case PropItemTypes.NUMBER: {
        return (
          <el-input-number
            v-model={form[prop]}
            {...commonProps}
            onInput={(value: number) => onChange(prop, value)}
          />
        )
        break
      }
      case PropItemTypes.BOOLEAN: {
        const uiWidget = uiItem[UI_WIDGET] || 'switch'
        const booleanOneOf = [
          { const: true, title: '' },
          { const: false, title: '' }
        ]
        if (uiWidget === UiWidgets.SWITCH) {
          return (
            <el-switch
              v-model={form[prop]}
              onChange={(value: any) => onChange(prop, value)}
              {...commonProps}
            />
          )
        } else if (uiWidget === UiWidgets.RADIO) {
          return (
            <el-radio-group
              v-model={form[prop]}
              onChange={(value: any) => onChange(prop, value)}
              {...commonProps}
            >
              { booleanOneOf.map(one => (
                <el-radio label={one.const}>{one.title}</el-radio>
              ))}
            </el-radio-group>
          )
        } else if (uiWidget === UiWidgets.SELECT) {
          return (
            <el-select
              v-model={form[prop]}
              onChange={(value: any) => onChange(prop, value)}
              {...commonProps}
            >
              {
                booleanOneOf.map(one => (
                  <el-option label={one.title} value={one.const}/>
                ))
              }
            </el-select>
          )
        } else {
          throw Error('boolean widgetswitch, radio, select')
        }
        break
      }
      case PropItemTypes.ARRAY: {
        if (anyOf && anyOf.length > 0) {
          const uiWidget = uiItem[UI_WIDGET] || 'select'
          if (uiWidget === UiWidgets.SELECT) {
            return (
              <el-select
                v-model={form[prop]}
                multiple
                onChange={(value: any) => onChange(prop, value)}
                {...commonProps}
              >
                {
                  anyOf.map(item => (
                    <el-option label={item.title} value={item.const}/>
                  ))
                }
              </el-select>
            )
          } else if (uiWidget === UiWidgets.CHECKBOX) {
            return (
              <el-checkbox-group
                v-model={form[prop]}
                {...commonProps}
                onChange={(value: any) => onChange(prop, value)}
              >
                {
                  anyOf.map(item => (
                    <el-checkbox label={item.const}>{item.title}</el-checkbox>
                  ))
                }
              </el-checkbox-group>
            )
          } else {
            throw Error('anyOf  select  checkbox')
          }
        }
        if (format) {
          // TODO 
          if (format === 'date') {
            return (
              <el-date-picker
                type="daterange"
                v-model={form[prop]}
                range-separator=" - "
                {...commonProps}
              >
              </el-date-picker>
            )
          }
        }
        break
      }
      default:
        return <div> {type}</div>
    }
  }
  const uiColumn = uiItem[UI_COLUMN] || 1
  const labelWidth = (uiItem[UI_OPTIONS] || {}).labelWidth === undefined ? 'auto' : (uiItem[UI_OPTIONS] || {}).labelWidth
  return (
    <el-col span={defaultSpan * uiColumn}>
      <el-form-item label={item.title} prop={prop} label-width={labelWidth}>
        { generateItem() }
      </el-form-item>
    </el-col>
  )
}
